.navbar {
    position: fixed;
    display: flex;
    justify-content: space-around;
    height: 55px;
    width: 100%;
    border-bottom: thin solid gainsboro;
    top: 0;
    left: 0;
    background-color: white;
    z-index: 100;
}

.nav-item {
    padding: 0px 5px;
    position: relative;
}

.iconset {
    display: flex;
    width: 45%;
    flex-direction: row;
    justify-content: flex-end;
}
